.body {
    width: 80vh;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
  }
  
  .form {
    background-color: #fff;
    margin-top: 3rem;
    width: 70vh;
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .form label {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .form input {
    margin-top: 1rem;
    padding: 0.5rem;
    border: 2px solid gray;
    height: 1.5rem;
    border-radius: .7rem;
  }
  
  .form .submit {
    margin-top: 2rem;
    font-size: 1rem;
    color: #fff;
    background-color: #000;
    padding: 0.5rem 1.4rem;
    border: none;
    border-radius: .5rem;
  }
  .item {
    background-color: #ffffff;
    padding: 1rem;
    margin-top: 1rem;
    min-width: 40vh;
    display: flex;
    justify-content: space-between;
    border-radius: .6rem;
  }
  .item h3 {
    font-size: 1rem;
    margin: 0;
    letter-spacing: .2rem;
  }
  .item .remove{
    background: none;
    border: none;
    color: rgb(0, 204, 204);
    letter-spacing: .1rem;

  }